<template>
  <div>
    <!-- 必须等数据执行后再渲染组件才行 -->
    <aplayer v-if="isShow" :music="musicList[0]" :list="musicList" :showLrc="true" />
  </div>
</template>
<script>
import aplayer from "vue-aplayer";
import axios from "axios";
export default {
  data() {
    return {
      musicList: [],
      isShow: false
    };
  },
  created() {
    let obj = { title: "音乐", color: "music" };
    this.$emit("changeNav", obj);
    this.getData();
    
  },
  components: {
    aplayer
  },

  methods: {
    getData() {
      //  public里的内容直接在根目录 所以路径是当前路径./data/musicdate
      axios.get("./data/musicdata.json").then(res => {
        let arr = res.data.musicData;
        arr.forEach((element, index) => {
          arr[index].lrc = location.origin + location.pathname + element.lrc;
        });
        this.musicList = arr;
        this.isShow = true;
      });
    }
  }
};
</script>
